<template>
  <div class="openAccount">
    <myHeader
      :isBack="true"
      backPath="/"
      title="我要开户">
    </myHeader>

    <div class="seat-head"></div>
    <!-- 申请条件 -->
    <section class="content">
      <mt-cell title="申请条件"></mt-cell>
      <div class="text">
        <p>1.自有合格栏舍或拟建符合要求猪舍(包括环保设施) 饲养规模达650头以上;</p>
        <p>2.具备基本养殖经验或愿意按照公司要求进行饲养；</p>
        <p>3.能够缴纳饲养保证金（具体标准按公司文件）。</p>
      </div>
    </section>
    <!-- 开户表单 -->
    <mt-field label="姓名：" placeholder="请输入您的姓名" v-model="openAccount.name"></mt-field>
    <mt-field label="身份证号：" placeholder="请输入您的身份证号" v-model="openAccount.personID"></mt-field>
    <mt-field label="猪舍地址：" placeholder="请输入地址" v-model="openAccount.pigstyAddress"></mt-field>
    <mt-cell title="开户服务部：" @click.native="searchAddress">
      <span class="introducer-placeholder">{{openAccount.openAddress}}</span>
      <span class="search-circle"></span>
    </mt-cell>
    <mt-cell title="介绍人：" @click.native="searchIntroducer">
      <span class="introducer-placeholder">{{openAccount.introducer}}</span>
      <span class="search-circle"></span>
    </mt-cell>
    <mt-cell title="介绍人所在服务部：">{{ openAccount.introducerBelongTo }}</mt-cell>
    <mt-cell title="介绍人身份证号：">{{ openAccount.cardID }}</mt-cell>
    <mt-field label="手机号：" placeholder="请输入手机号" v-model="openAccount.tel">

    </mt-field>
    <mt-field label="手机验证码："  v-model="openAccount.codeNumber" placeholder="请输入验证码">
      <mt-button  size="small" @click="getCodeData()"  :style="{background:showBgColor,color:isWhite}">{{codeTitle}}</mt-button>
    </mt-field>

    <div class="takeCare-buy">
      <img @click="agreeReadHandler" v-if="isAgreeRead" class="select-btn" src="../../assets/select-icon.png">
      <img @click="agreeReadHandler" v-else class="select-btn" src="../../assets/noSelect-icon.png">
      本人已阅读并同意<router-link to="/openText" class="mark-text">《正邦养户服务号使用协议》</router-link>
    </div>
    <SubmitBtn @click.native="submitData"></SubmitBtn>
  </div>
</template>

<script>
  import myHeader from "@/components/Header";
  import {putApplyOpenAccount} from '@/api/userCenter'
  import SubmitBtn from "@/components/SubmitBtn";
  import {submitValidator} from '@/utils/validator'
  import {bindUser,getCode,getUserInfo} from  "@/api/bindInfo"
  import {Toast} from 'mint-ui';
  import wx from "weixin-js-sdk";
  import {mapGetters} from "vuex";


  export default {
    name: "Register",
    data() {
      return {
        isAgreeRead: false,
        openAccount: {
          name: '',
          personID: '',
          pigstyAddress: '',
          openAddressID:'',
          tel: '',
          introducer: '',
          codeNumber:'',
          cardID:'',
          openAddress: '',
          introducerBelongTo: '',
          introducerID: '',
        },
        codeTitle: "验证码",
        isSend: true,
        showBgColor:'#409EFF',
        isWhite:'white'
      };
    },
    components: {
      SubmitBtn,
      myHeader
    },
    created(){
      this.initWxConfig();
    },
    mounted() {
    },
    beforeRouteEnter  (to, from, next) {
      next(vm => {
        if (from.name == 'searchIntroducer' && sessionStorage.getItem('introducerInfo')) {
          vm.openAccount.introducer = JSON.parse(sessionStorage.getItem('introducerInfo')).name;
          vm.openAccount.introducerBelongTo = JSON.parse(sessionStorage.getItem('introducerInfo')).belongTo;
          vm.openAccount.cardID = JSON.parse(sessionStorage.getItem('introducerInfo')).cardID;
          vm.openAccount.introducerID = JSON.parse(sessionStorage.getItem('introducerInfo')).introduceId;
        }
        if (from.name == 'searchAddress' && sessionStorage.getItem('addressName')) {
          vm.openAccount.openAddress = JSON.parse(sessionStorage.getItem('addressName')).name;
          vm.openAccount.openAddressID = JSON.parse(sessionStorage.getItem('addressName')).cardID;
        }
      });

    },
    methods: {
      agreeReadHandler() {
        this.isAgreeRead = !this.isAgreeRead;
      },
      searchIntroducer() {
        this.$router.push("/searchIntroducer");
      },
      searchAddress(){
        this.$router.push("/searchAddress");
      },
      getCodeData(){
        let  _this = this;
        if (_this.openAccount.tel !== "") {
          if (/^[1][3,4,5,7,8,9][0-9]{9}$/.test(this.openAccount.tel)) {
            this.showBgColor="grey";
            if (_this.isSend) {
              var InterValObj; //timer变量，控制时间
              var count = 60; //间隔函数，1秒执行
              var curCount; //当前剩余秒数
              curCount = count;
              this.codeTitle = curCount + "秒";
              InterValObj = window.setInterval(SetRemainTime, 1000);
              _this.isSend = false;
              this.showBgColor="grey";
              getCode(_this.openAccount.tel)
                .then(res => {
                  _this.isSend = false;
                })
                .catch(err => {
                  window.clearInterval(InterValObj);
                  _this.codeTitle = "重新发送";
                  _this.showBgColor="#409EFF";
                  _this.isSend = true;
                  _this.formData.invitationCode = "";
                  Toast({
                    message: "发送失败",
                    duration: 1000
                  });
                });
            }
          } else {
            Toast({
              message: "手机号码错误！",
              duration: 1000
            });
          }
        } else {
          Toast({
            message: "手机号码不能为空！",
            duration: 1000
          });
        }
        function SetRemainTime() {
          //计时器
          if (curCount == 0) {
            window.clearInterval(InterValObj);
            _this.isSend = true;
            _this.codeTitle = "重新发送";
            _this.showBgColor="#409EFF";
            _this.formData.invitationCode = "";
          } else {
            _this.isSend = false;
            curCount--;
            _this.codeTitle = curCount + "秒";
          }
        }
      },
      initWxConfig() {
        wx.config({
          debug: false, //
          appId: this.signature.appId, // 必填，企业号的唯一标识，此处填写企业号corpid
          timestamp: this.signature.timestamp, // 必填，生成签名的时间戳
          nonceStr: this.signature.nonceStr, // 必填，生成签名的随机串
          signature: this.signature.signature, // 必填，签名，见附录1
          jsApiList: [
            "closeWindow"
          ] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
        });
      },
      submitData() {
        const needCheckList = [{
          val: this.openAccount.name,
          rule: ['required', 'standard'],
          type: 'ChineseName'
        }, {
          val: this.openAccount.personID,
          rule: ['required', 'standard'],
          type: 'IDCard'
        }, {
          val: this.openAccount.pigstyAddress,
          rule: ['required'],
          getTipText: '猪栏地址'
        }, {
          val: this.openAccount.openAddress,
          rule: ['required'],
          getTipText: '开户服务部'
        }, {
          val: this.openAccount.tel,
          rule: ['required', 'standard'],
          type: 'tel'
        }, {
          val: this.openAccount.codeNumber,
          rule: ['required'],
          getTipText: '手机验证码'
        }, {
          val: this.isAgreeRead,
          rule: ['requiredChecked'],
          type: 'agreeAppointment'
        }];

        if (!submitValidator(needCheckList)) return;
        let data = {
          "serviceId": this.openAccount.openAddressID,
          "serviceName":this.openAccount.openAddress,
          "introducerId": this.openAccount.introducerID,
          "introducer":this.openAccount.introducer,
          "introducerDept":this.openAccount.introducerBelongTo,
          "invitationCode": this.openAccount.codeNumber,
          "idno": this.openAccount.personID,
          "custName": this.openAccount.name,
          "pigpenList":[{
            "pigpenAddress":this.openAccount.pigstyAddress
          }],
          "telNum": this.openAccount.tel
        };
        putApplyOpenAccount(data).then((res) => {
          this.$toast({
            message: '开户资料提交成功，待后台1-2个工作日审核后再来体验更多功能！',
            duration: 2000
          });
          setTimeout(()=>{
            wx.closeWindow();
          },3000)
        }, err => {
          this.$toast({
            message: err,
            duration: 1000
          });
        })

      }
    },
    computed: {
      ...mapGetters(["signature"])
    }
  };
</script>

<style scoped lang="scss">
  .openAccount {
    min-height: 100vh;
    background-color: #f8f8f8;
    .content {
      height: 1.88rem;
      margin: 0.1rem 0;
      background-color: #fff;
    }
    .text {
      height: 1.42rem;
      padding: 0.1rem 0.14rem;
      font-size: 0.15rem;
      line-height: 0.3rem;
    }

    .introducer-placeholder {
      color: #888888;
      margin-right: .1rem;
    }

    .search-circle {
      border-radius: 50%;
      width: .20rem;
      height: .20rem;
      background: url("~@/assets/icon-search.png");
      background-size: .2rem .2rem;
    }

    .takeCare-buy {
      font-size: .14rem;
      margin: .3rem .16rem;
      text-align: center;

      .select-btn {
        width: .16rem;
        height: .16rem;
        vertical-align: text-bottom;
        margin-right: .1rem;
      }

      .mark-text {
        color: #FF6A1D;
      }
    }
  }
</style>



